.feedback {
  margin-top: -51px;
  padding: 0 calc(50% - 585px);

  background-color: @main-grey-color;
}
.feedback-post {
  align-items: center;
  width: 775px;
  margin-top: 50px;
  margin-left: 198px;

  font-size: 36px;
  line-height: 41px;
  font-family: @font-thin;
  text-align: center;
  color: @white-color;

  .flex-mixin(@flex-direction: column);
}

.feedback-post__text {
  width: 775px;
  margin-bottom: 14px;
  padding-top: 16px;
}

.feedback-post__user-name {
  margin-top: -4px;

  font-style: normal;
  font-size: 16px;
  line-height: 41px;
  font-family: @font-bold;
  text-transform: uppercase;
}

.feedback-post__user-img {
  width: 70px;
  height: 70px;

  border-radius: 50%;

  object-fit: cover;
}

.feedback__slider {
  position: relative;
  bottom: 22px;
  left: 6px;

  padding-bottom: 8px;

  list-style: none;

  .flex-mixin(@justify-content: center);
}

.feedback__button {
  display: block;
  width: 11px;
  height: 11px;
  margin-right: 10px;
  padding: 0;

  background-color: transparent;
  border: 2px solid @white-color;
  border-radius: 50%;
  &:hover {
    background-color: @white-color;
    cursor: pointer;
  }
  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px @main-grey-color, 0 0 0 4px @white-color;
  }
}

.feedback__button-active {
  background-color: @white-color;
}
